import { getUser, updateUser } from "@/api/user";
export default {
  middleware: "auth",
  name: "SettingsIndex",
  data() {
    return {
      user: {
        image: null,
        email: null,
        bio: null,
      },
      isSaving: false,
    };
  },
  async asyncData() {
    const { data } = await getUser();
    const { user } = data;
    return {
      user: user,
    };
  },
  methods: {
    updateSetting() {
      this.isSaving = true;
      const { data } = updateUser({
        user: this.user,
      });
      this.isSaving = false;
    },
  },
};
